document.addEventListener('DOMContentLoaded', function(){
  iniciarApp();
});

function iniciarApp() {
  navegacion();
  pantalla();

}
//Variables globales--
const pantallaCompleta = document.querySelector(".pantallaCompleta");
const body = document.querySelector(".body");
const menuBtn = document.querySelector("#menu-toggle");
const nav = document.querySelector(".nav");

function navegacion() {

  menuBtn.addEventListener('click', function(){
    menuBtn.classList.toggle("open");
    nav.classList.toggle("muestra");
    body.classList.toggle("fijar-body");
    pantallaCompleta.classList.toggle("activarPantallaNegra");
    
  });
}

function pantalla() {

  pantallaCompleta.addEventListener('click', function(){
    menuBtn.classList.remove("open");
    nav.classList.remove("muestra");
    body.classList.remove("fijar-body");
    pantallaCompleta.classList.remove("activarPantallaNegra");
    
  });
}


